  .navbar-profile-pic img {
    border-radius: 90px;
    height: 180px;
    width: 180px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
    /* margin-top: 10px; */

  }

  .nav-item{
    color: white;
    margin-top: 30px;
    text-align: center;
    margin-left: 30px;
  }

  .nav-link{
    margin-top: 20px;
    margin-left: 0;
    cursor: pointer;
  }

  nav-link svg{
    margin-top: 10px;
  }

  .nav-link:hover{
    color: #f29f67;
  }

  .nav-link .active{
    color: #f29f67;
    border-left: 5px solid #f29f67;
    padding-left: 10px;
    text-decoration: none;
  }