.work{
  background-color: #1387812a;
  margin-left: 140px;
}
.work-exp{
    padding: 60px 0px;

}
.work-exp h2{
  font-weight: bold;
    color: #1e1e2c;
}

.work-exp  hr{
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.work-exp .vertical-timeline-element-title{
  font-weight: bold;
  color: #138781;
}

.work-exp .vertical-timeline-element-subtitle{
  color: #1e1e2ca2;
}

@media (max-width:600px){
  .work{
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .work hr{
    width: 280px;
  }
}